/* BELOW - TEST SVG CSS CODE */
.main {
  stroke-dasharray: 50;
  stroke-dashoffset: 0;
  animation: load 5s linear infinite;
  transform-origin: center;
}

@keyframes load {
  0% {
    transform: rotate(0deg);
    stroke-dashoffset: 50;
  }
  50% {
    stroke-dashoffset: 0;
  }
  100% {
    transform: rotate(360deg);
    stroke-dashoffset: -50;
  }
}

#seoul {
  fill:#003049;
}

#south {
  fill: #ff595e;
}
#west {
  fill: #FFD166;
}
#north {
  fill: #118AB2;
}
#center {
  fill: #06D6A0;
}
#east {
  fill: #6a4c93;
}

:is(#center, #east, #west, #south, #north):active,
:is(#center, #east, #west, #south, #north):hover {
  stroke: #fff;
  fill: #000; 
  position: relative;
  filter: drop-shadow(0px 15px 0px #ef476f);
  transition: 0.5s;
}
